<template>

  <div>

    <van-nav-bar
      title="背忘录" />

    <div class="all">

      <div class="value">

      </div>

      <!-- <div class="translation">
      <el-input
        type="textarea"
        :rows="2"
        placeholder="请输入单词"
        v-model="textarea"
        size="medium"
        id="value"
        ref="value">
      </el-input>
      

    </div> -->
      <h3>请输入单词</h3>
      <van-field
        v-model="textarea" />

      <!-- <p v-if="this.success">{{this.word}}  录入成功</p> -->

      <div class="wrap">
        <div
          v-if="this.addWordList.length"
          class="left">
          <h5>录入的单词</h5>
          <ul id="addWordList"
            v-for="(w,index) in this.addWordList"
            :key="index">
            <li>{{w}}</li>

          </ul>
        </div>
      </div>
      <!-- <el-button type="primary"
      plain id="btn3"
      @click="addWord">录入
    </el-button> -->

      <van-button plain
        type="info" id="btn3"
        @click="addWord">录入
      </van-button>

    </div>
  </div>

</template>
<script>
import axios from 'axios'
import { Toast } from 'vant'
export default {
  name: 'AddWords',
  data() {
    return {
      textarea: '',
      success: false,
      word: '',
      addWordList: [],
    }
  },

  methods: {
    onClickLeft() {
      Toast('返回')
      history.go(-1)
    },
    onClickRight() {
      Toast('按钮')
    },
    onClickEnter() {
      this.addWord()
    },
    open4(msg) {
      this.$notify.error({
        title: 'warning',
        message: msg,
        type: 'warning',
      })
    },
    addWord() {
      let regx = /^[A-Za-z\x20\x2d]+$/

      if (this.textarea == '' || this.textarea == null) {
        this.open4('请输入内容')
        this.textarea = ''
      } else {
        if (!regx.test(this.textarea)) {
          this.open4('请输入单词')
          this.textarea = ''
        } else {
          axios({
            method: 'post',
            url: this.$baseURL + '/word/addWord',
            data: {
              uid: this.$store.state.uid,
              value: this.textarea,
            },
          }).then((res) => {
            // console.log(res)
            if (res.data.status == 200) {
              this.success = true
              this.word = this.textarea
              //给数组开头添加单词进去,达到倒序显示的效果
              this.addWordList.unshift(this.word)
              console.log(this.addWordList)
              this.open1(this.word)
              this.textarea = ''
            } else {
              this.success = false
              this.textarea = ''
              // alert(res.data.message)

              this.open4(res.data.message)
            }
          })
        }
      }
    },
    open1(word) {
      this.$notify({
        title: '添加成功',
        message: '单词' + word + '添加成功',
        type: 'success',
        position: 'bottom-right',
      })
    },
  },
  mounted() {
    this.$keyBoard(this, 'onClickEnter', 13) //13是enter按键  其他按键码自己查
    if (
      this.$store.state.uid == null ||
      this.$store.state.uid == '' ||
      this.$store.state.uid === undefined
    ) {
      alert('请先登录')
      this.$router.push('/phone/login')
    }
  },
}
</script>
<style scoped>
#btn3 {
  position: absolute;

  top: 13rem;
  right: 3.125rem;

  font-size: 1.5rem;
}

ul li {
  list-style: none;
}

#addWordList {
  margin-left: 2.1875rem;
}
.van-field {
  border: 2px solid black;
  border-radius: 5px;
}

/* * {
  margin: 0;
  padding: 0;
} */

/* .wrap {
  display: flex;
  position: absolute;
  height: 100%;
  width: 100%;
} */

p {
  text-align: center;
}

.left {
  width: 13rem;
  height: 31rem;

  overflow: auto;
}

/* 修改滚动条样式 */
.left::-webkit-scrollbar {
  /* 
                width height分别对应竖向滚动条和横向滚动条
            */
  width: 4px;
  height: 4px;
  background-color: #ffffff;
}
/* 修改滚动条中的小滑块 */
.left::-webkit-scrollbar-thumb {
  width: 10px;
  border-radius: 5px;
  background-color: #5592fa;
}
/* 定义滚动条的轨道 */
.left::-webkit-scrollbar-track {
  background-color: white;
}

/* 定义轨道两端的按钮 */
.left::-webkit-scrollbar-button {
  background-color: green;
}

.left::-webkit-scrollbar-thumb:hover {
  background-color: hotpink;
}
</style>
